.hidden {
  display: none;
}

.breed-select {
  height: 50px;
  width: 100%;
  max-width: 380px;
  border: 1px solid #ccc;
  border-radius: 10px;
  margin: 0;
  padding: 0 20px;
  font-size: 18px;
}

.container {
  width: 50%;
  padding: 10px;
}

.cat-info {
  width: fit-content;
}

img {
  width: 100%;
  object-fit: cover;
  margin-top: 20px;
}

.error {
  color: red;
}

.loading {
  width: 20px;
  height: 20px;
  box-sizing: border-box;
  border-top: 3px solid #4c4a4a;
  border-right: 3px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

